@import "../../global";

.intro {
    background-color: white;
    display: flex;
    min-height: 100vh; // Ensure it takes up at least the full viewport height

    @include mobile {
        flex-direction: column;
        align-items: center;
        font-size: 15px;
    }

    .left {
        flex: 0.5;
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 50px;

        @include mobile {
            padding: 30px;
        }

        .imageContainer {
            width: 450px; // Set a fixed width
            height: 450px; // Set a fixed height
            background-color: whitesmoke;
            border-radius: 50%;
            overflow: hidden;

            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
    }

    .right {
        flex: 0.5;
        display: flex;
        flex-direction: column;
        justify-content: center;
        padding: 5% 7% 10% 10%;

        @include mobile {
            padding: 5%;
        }

        .right_above {
            width: 100%;
            margin-bottom: 30px;

            .typewriter {
                font-family: "Lucida Console", "Courier New", monospace;
                font-size: 24px;
                line-height: 1.6;

                @include mobile {
                    font-size: 18px;
                }

                .colored {
                    color: #ffa07a;
                }
            }
        }

        .play {
            margin-top: 30px;
        }

        .imageContainer2 {
            width: 100%;
            display: flex;
            justify-content: center;
            margin-top: 20px;

            img {
                width: 100px;
                height: 100px;
                object-fit: contain;

                @include mobile {
                    width: 80px;
                    height: 80px;
                }
            }
        }
    }
}