@import "../../global";

.contact {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 50px 10%;

  .left {
    display: flex;
    justify-content: center;
    width: 100%;
    margin-bottom: 50px;

    a {
      display: flex;
      justify-content: center;
      width: 10%;
      margin: 0 10px;

      @include mobile {
        width: 20%;
      }

      img {
        width: 50%;
      }
    }
  }

  .right {
    width: 100%;
    max-width: 500px;

    h2 {
      font-size: 30px;
      text-align: center;
      margin-bottom: 20px;
    }

    form {
      display: flex;
      flex-direction: column;
      align-items: center;

      input, textarea {
        width: 100%;
        margin-bottom: 20px;
        padding: 10px;
        font-size: 14px;
      }

      textarea {
        height: 200px;
      }

      button {
        width: 150px;
        height: 40px;
        background: $mainColor;
        color: white;
        border: none;
        border-radius: 5px;
        cursor: pointer;
      }
    }
  }
}