/* menu.scss */

@import '../../variables';

.menu {
  position: fixed;
  top: 0;
  right: -250px; /* Width of the menu */
  width: 250px;
  height: 100%;
  background-color: $sideColor;
  color: #fff;
  transition: left 0.3s ease-in-out;
  z-index: 5;

  ul {
    list-style: none;
    padding: 0;
    margin-top: 70px; /* Adjust as needed */
  }

  li {
    padding: 15px 20px;

    a {
      color: #fff;
      text-decoration: none;

      &:hover {
        text-decoration: underline;
      }
    }
  }
}

.menu-hover-area {
  position: fixed;
  top: 0;
  right: 0; /* Position the hover area on the right */
  width: 20px; /* Adjust the width for easier hover */
  height: 100%;
  z-index: 10; /* Above other elements */
}

/* Show the menu when hovering over the hotspot or the menu itself */
.menu-hover-area:hover ~ .menu,
.menu:hover {
  right: 0;
}
